				<p>题注:般若波罗密多心经</p>
				<p>般若：就是大智慧。</p>
				<p>即把愚痴虚妄之心，去得干干净净，把自己本来就有的真实之心，发露出来的大智慧。而不是贪图功利的小智慧。</p>
				<p>波罗密：就是到彼岸。即离苦得乐，转迷为悟的彼岸。</p>
				<p>人的一切欲念，皆由心生，皆由心起。一心向善，即可成佛;一心念恶，即堕地狱。不论什么境界，都没有虚空那样，大得没有边际，独有这个心，可以包含虚空。所以有须弥纳芥子之说。</p>
				<p>本文的主旨就是：明白真实的道理，迈向生命的圆满。</p>
				<p>观自在：即观世音菩萨。</p>
				<p>观自在是法号。观就是置身局外，从旁观察。本处更是如此，用自己清净心里头本来就具有的慧眼，来观照自己的妙真如性。</p>
				<p>菩萨：就是用佛法来教化、普渡众生的人。 菩萨是菩提萨简称。</p>
				<p>菩提是佛道，萨是成就众生。</p>
				<p>就是用觉悟之道来教化众生，使得众生都能觉悟。</p>
				<p>行:</p>
				<p>就是依照用大智慧到彼岸的方法，去修。</p>
				<p>深：</p>
				<p>是形容修的功夫的程度。</p>
				<p>般若：(bo re)智慧。</p>
				<p>般若有几种：</p>
				<p>文字般若：文字，即经的字句，经里头的种种道理，全靠文字来叙述，来表达。但是文字本身并不是真如本性。所以六祖惠能说：“诸佛妙理，非关文字”，故禅宗有“教外别传，不立文字，直指人性，见性成佛。”</p>
				<p>境界般若：又名观照般若。观就是观察，观察研究种种法的景象。照就是烛照，用光明心去照见万象的义理。一切法和义理，如果不经过观察照见的功夫，又那里能够彻底明白呢。有境界，就有次第。如：“昨夜西风凋碧处，独上高楼，望尽天涯路”。“衣带渐宽终不悔，为伊消得人憔悴”。“众里寻他千百度，蓦然回首，那人却在，灯火阑珊处”。</p>
				<p>实相般若：实相，就是真实相。实相无相，凡是真实的，都是没有形相的。所以金刚经说：“凡所有相，皆是虚妄”。所以无相的.，才是真实的。那么无相的究竟是什么呢?就是自心的本体，如来藏心。</p>
				<p>照见：</p>
				<p>用慧眼来观照自己的自性。正确地认识，明彻地看见自己的自性。</p>
				<p>五蕴：</p>
				<p>色、受、想、行、识五种。</p>
				<p>色：眼、耳、鼻、舌、身五根。</p>
				<p>受：领受。通过眼、耳、鼻、舌、身五根领受色、声、香、味、触五尘。</p>
				<p>想：转念头。由五根领受到五尘而产生出的种种妄心。</p>
				<p>行：急水上打球，念念不停留。一个接一个的乱念头时起时灭，没有停歇。</p>
				<p>识：就是分别。 分别那种种东西境界。</p>
				<p>由于有了这色、受、想、行、识五种蕴，积聚盘结在心头里，就使得人迷迷惑惑，把原来的真实心，智慧光，一齐遮盖住了，这真心的清净光明，也就显露不出来了。</p>
				<p>皆：</p>
				<p>全。</p>
				<p>空：</p>
				<p>不包含什么，不真实，不长久，无常。</p>
				<p>用慧眼来看，有形之物必然消亡，都是不长久的，只是计量的尺度不同罢了。即使清净之心，也是空的，无拘无碍，自由自在的。</p>
				<p>佛的人生观，就是要我们直面人生。人生短暂，生命只在呼吸间。当苦来临时，就化成苦;当乐来临时，就化成乐。通过自身的刻苦磨砺，去得到心灵的安宁。</p>
				<p>度：救度。</p>
				<p>厄：灾难。</p>
				<p>度一切苦厄：就是救度一切众生脱离苦难。</p>
				<p>舍利子：释迦牟尼佛的弟子，智慧最为第一。</p>
				<p>舍利子是法号，就是舍掉自我私利的人。</p>
				<p>色不异空，空不异色，色即是空，空即是色：</p>
				<p>色相与空相没有分别、空相与色相也没有分别，色相就是空相，空相就是色相。不要执着于色相，而看不到空相;也不要执着于空相，而看不到色相。</p>
				<p>观自在菩萨所讲的是大乘佛法，圆通自在，空灵无碍，色空无二。</p>
				<p>受、想、行、识，亦复如是：</p>
				<p>受、想、行、识也和色一样，与空无分别。</p>
				<p>诸法空相：</p>
				<p>所有的法，都是空相。没有分别相。</p>
				<p>不生不灭，不垢不净，不增不减：</p>
				<p>既然是空相，那么也就没有生与死、垢与净、增与减的分别。</p>
				<p>人的自性，本身就是空灵的，清净无碍的。众生迷惑，执着于一头，自然不知其理。</p>
				<p>是故：</p>
				<p>正是这个空的缘故。</p>
				<p>观自在菩萨更进一步地向我们阐述一切皆空，这一玄奥的道理。</p>
				<p>空中无色，无受、想、行、识：</p>
				<p>色、受、想、行、识这五蕴也是空的。</p>
				<p>无眼、耳、鼻、舌、身、意：</p>
				<p>眼、耳、鼻、舌、身、意这六根也是空的。即六根清净。</p>
				<p>无色、声、香、味、触、法：</p>
				<p>色、声、香、味、触、法这六尘也是空的。</p>
				<p>无眼界，乃至无意识界：</p>
				<p>即十八界也是空的。</p>
				<p>六根界：眼界、耳界、鼻界、舌界、身界、意界;</p>
				<p>六尘界： 、声界、香界、味界、触界、法界;</p>
				<p>六识界：眼识界、耳识界、鼻识界、舌识界、身识界、意识界。</p>
				<p>无无明,亦无无明尽,乃至无老死,亦无老死尽：</p>
				<p>即这十二因缘也都是空的。</p>
				<p>十二因缘：</p>
				<p>无明、行、识、名色、六入、触、受、爱、取、有、生、老死。</p>
				<p>空中自然没有这十二因缘，也没有这十二因缘役使。</p>
				<p>法句经中释迦说：</p>
				<p>勤奋努力是不死的境界，懒惰懈怠是死境界。</p>
				<p>勤奋努力虽死犹生，懒惰懈怠虽生犹死。</p>
				<p>无苦、集、灭、道：</p>
				<p>四谛也还是空的。</p>
				<p>四谛：</p>
				<p>苦：凡夫的现状，病状。</p>
				<p>集：苦之原因，病因。</p>
				<p>灭：觉悟状态，健康相。</p>
				<p>道：实现理想的方法，治疗法。</p>
				<p>无智也无得：</p>
				<p>既然一切皆空，那么智和得也是空的了。</p>
				<p>去法我执，即把人生的最高追求欲望，也要空掉，不要执著。</p>
				<p>以无所得故：</p>
				<p>正是因为没有什么可以得到的缘故。</p>
				<p>只有明白了一切皆空的道理，本来就有的妙真如性，就会显现出来。妙真如性，是众生本身就具有，又何需得到?</p>
				<p>金刚经说，无有少法可得。倘若有法可得，即著相。</p>
				<p>凡所有相，皆是虚妄。就不是佛法。</p>
				<p>只有破人我执，破法我执，把一切虚妄相破掉，心里头才湛然清净，无拘无执，成就佛道。</p>
				<p>心无挂碍，无挂碍故，无有恐怖：</p>
				<p>挂：牵挂。碍：阻碍。</p>
				<p>因为依照般若波罗密多的方法修行，所以不为物所拘，无牵挂滞碍。</p>
				<p>没有牵挂，也就没有恐怖心理。</p>
				<p>人只要有滞碍、有牵挂，就永远无法摆脱苦恼的纠缠。</p>
				<p>远离颠倒梦想:</p>
				<p>正确地认识真理，远离一切错误想法和梦幻想法。</p>
				<p>究竟涅盘：</p>
				<p>究竟：毕竟，到底的意思。</p>
				<p>涅盘：超越生死的境界。</p>
				<p>开悟是人生的最终目标，因此尽形寿不懈怠，最终超越一切境相。</p>
				<p>三世诸佛：</p>
				<p>三世：过去世、现在世、未来世。</p>
				<p>诸佛：一切佛。</p>
				<p>阿耨多罗三藐三菩提：</p>
				<p>阿耨多罗：无上的。</p>
				<p>三藐三菩提：正等正觉，真正觉悟的人。</p>
				<p>故知：</p>
				<p>所以我们了解。</p>
				<p>咒：即陀罗尼。真言，总持。总持：就是总纲。一法普现一切法，一咒普含一切咒。</p>
				<p>大神咒：具有大神力的咒。</p>
				<p>大明咒：具有大光明的咒。</p>
				<p>无上咒：具有至高无上的咒。</p>
				<p>无等等咒：具有无与伦比的咒。</p>
				<p>能除一切苦：</p>
				<p>它能除尽人心中的一切烦苦。</p>
				<p>真实不虚：</p>
				<p>是真实的，不是虚假的。</p>
				<p>即说咒曰：</p>
				<p>那么，就让我来读诵咒语吧。</p>
				<p>揭谛揭谛,波罗揭谛,波罗僧揭谛,菩提萨婆诃!</p>
				<p>此四句是密咒，要绝对明确地译出来是极为困难的，因此只需念诵，不需翻译。</p>
				<p>如果强译，可理解为：</p>
				<p>觉悟，觉悟，正在觉悟，已经觉悟，就这样成就了佛道。</p>
				<p>波罗即到彼岸，波罗僧即已经到达彼岸者。</p>
				<p><strong>折叠唐三藏沙门鸠摩罗什11译</strong></p>
				<p>般若。智慧也。波罗蜜。到彼岸也。多。定也。经。径也。言此经乃定心之径路也。此经以单法为名。实相为体。观照为宗。度苦为用。大乘为教相。此五者。经中所说之旨也。单法者。即般若波罗蜜多也。实相者。即诸法空相也。观照者。即照见五蕴皆空妄也。度苦者。即度一切业报苦厄也。大乘者。即菩萨所行甚深般若也。</p>
				<p>观自在菩萨。</p>
				<p>观自在者。观听圆明。智慧无碍。观有不住有。观空不住空。心不能动。境不能随。动随不乱其真。得大自在圆通也。菩萨。即菩提萨之省文。菩提曰觉。萨曰有情。谓能觉一切有情。自觉以觉众生也。曰菩萨曰大士。皆尊称佛号之名也。观自在菩萨。即观世音大士也。</p>
				<p>行深般若波罗蜜多时。</p>
				<p>行深者。谓清净因地之法行已深也。般若。华言智慧。般若智慧。非识心之昭昭灵灵。乃本心真性之光明。照无量世界。通无量劫事。圆明普照之真智真慧也。波罗蜜。华言到彼岸。此岸。乃众生作孽受苦。堕落沉沦之地。彼岸。乃诸佛菩萨究竟到人欲尽净。光明正大之地。多者。定也。时者。时候也。谓菩萨妙圆明觉超越世出世间。具足三昧慧定之时也。</p>
				<p>照见五蕴皆空。</p>
				<p>照者。本心智慧妙觉明照也。见者。本心智慧明圆真见也。五蕴者。色受想行识之五阴也。空即本心性体实相之真空也。照见蕴空。则本心妙明而性体洞见也。</p>
				<p>度一切苦厄。</p>
				<p>谓解脱一切执著生死烦恼之苦厄。成就一切圆通也。</p>
				<p>舍利子。</p>
				<p>即舍利弗。母名舍利。因母立名。乃佛之弟子。舍利弗于佛弟子之中。智慧第一。而不能如观世音菩萨究竟得证圆通。而成正觉者。盖因滞于智慧也。滞于智慧。则有我相。我见。(空相。空见。)佛故举观自在菩萨之行深般若真空无相之法。以证菩提彼岸。得为圆明普照之标榜。呼舍利子而告之。欲其不滞于我相。我见。空相。空见。而究竟于人空法空。空空之境界。以证无上道也。</p>
				<p>色不异空。空不异色。色即是空。空即是色。受想行识。亦复如是。</p>
				<p>色乃梦幻泡影。故不异于空也。</p>
				<p>空乃一真显露。故不异于色也。</p>
				<p>色有形相。凡有所相。皆是虚妄。终有坏期。故色即是空也。</p>
				<p>空性虚无。无形无声者也。道以虚无之体。视之不见。听之不闻。能生有色有声之天地万物。故空即是色也。</p>
				<p>因色而有六根之领受。六根领受于心。而生思量拟度之想。悬想六尘。而生运用施为之行。行动迁流。而生分辨精粗美恶之识。色受想行识。五者相因也。色有坏时。而受想行识。终归虚妄。亦复如是。色即是空也。能了悟色即是空。逢色不受。则无受想行识诸杂念一心清净。空若太虚。以无相而生真空之实相。亦复如是空即是色也。</p>
				<p>舍利子。是诸法空相。不生不灭。不垢不净。不增不减。</p>
				<p>佛复呼舍利子之名。进一层以告之也。意谓不但五蕴皆空。一切修证菩提之法。莫不皆空。楞伽经云。一切法不生。如是一切法空。若执著于法。则有法生。法生则心意意识皆生。有生则有灭。有生灭。则有垢净增减矣。诸法若如虚空界之空相。岂有生灭垢净增减乎。</p>
				<p>是故空中无色。无受想行识。无眼耳鼻舌身意。无色声香味触法。</p>
				<p>是故承上起下之词。是故真空之中本无我相。何色之有。</p>
				<p>既无我相。自无我见。一切声色何能入我真空妙明之心。自无受想行识矣。</p>
				<p>既无我相。则六根亦非我有。无眼耳鼻舌身意矣。</p>
				<p>六根既无。则六尘无安顿之所。自无色声香味触法矣。</p>
				<p>无眼界。乃至无意识界。</p>
				<p>眼为六根之先锋。意为六识之主帅。而六根六尘。无识不显。无眼界。乃至无意识界。则六根六尘六识之十八界皆无界限矣。</p>
				<p>无无明。亦无无明尽。乃至无老死。亦无老死尽。</p>
				<p>
					无明至老死。乃佛所说三世十二因缘也。一曰。无明。谓过去世。妄识迷性也。二曰行。谓过去世。一切结业。随无明妄识流转也。三曰识。谓现在世。托胎识灵种子也。四曰名色。谓现在世。识灵在胎。心但有名。而色身已有质也。五曰六入。谓现在世。识灵在胎。已成六根之体。已具六入之用也。六曰触。谓现在世。色身出胎之后。六根但有所触。尚未了知六尘之涉入也。七曰受。谓现在世。色身日渐长大。已能纳受六尘诸境。尚于贪淫等心未起也。八曰爱。谓现在世。色身日益长成。贪淫等心已开。而生种种爱欲。尚未实爱欲之境也。九曰取。谓现在世。色身强壮。爱欲日盛。而驰求恣取色声香味触等以实爱欲之境也。十曰有。谓现在世。色身因驰求恣取。六根积集欲界。
					。无
					。三界之因。成就欲有。色有。无色有。三有之业果也。十一曰生。谓因果不亡。识灵又随业流转。受生于未来世之四生六道也。十二曰老死。谓未来世。受生以后。色身衰老。又坏而死也。因无明缘行。因行缘识。因识缘名色。因名色缘六入。因六入缘触。因触缘受。因受缘爱。因爱缘取。因取缘有。因有缘生。因生缘老死。十二因缘。生灭无常。有起有尽。尽而复起。起而复尽。循环三世。生灭轮转。若观此十二因缘。皆为无明妄识流转。忽然觉悟五蕴十八界皆空。则本妙明心。如如不动。无有生灭之妄识。则无无明矣。既无无明。则无起无尽。亦无无明尽矣。乃至者。包无行亦无行尽。无识亦无识尽。无名色亦无名色尽。无六入亦无六入尽。无触亦无触尽。无受亦无受尽。无爱亦无爱尽。无取亦无取尽。无有亦无有尽。无生亦无生尽。无老死亦无老死尽。
				</p>
				<p>无苦集灭道。无智亦无得。</p>
				<p>苦。谓生死烦恼诸苦。集。谓积聚。人生在世。心为形役。形为事劳。积聚五蕴中之根尘处界。十二因缘生死烦恼。种种苦因。今既无五蕴根尘处界。十二因缘。则无苦因之积聚也。</p>
				<p>灭。谓寂灭。道。谓真常不息之道。五蕴中一切苦因。一一皆生灭无常。今生灭既灭。寂灭现前。而当体妙明寂静之真性。洞彻圆通。以入真常不息之道矣。</p>
				<p>谓既无苦集灭道。则已入佛慧。明了无碍之智。已得菩提之果。然虽入佛智。乃本心般若真空之本智。般若无知。无所不知。无漏之智。如无智也。虽得菩提之果。亦是本心现量。即心即佛。不假外求。亦无所得也。若知有智。乃曲心推测之知见。非真空无漏之本智也。若云有所得。即有所失。亦有漏也。本心具足本智。故云无智亦无得也。</p>
				<p>以无所得故。</p>
				<p>此承上起下之词。谓一心之本智本法。本自具足。无心外之法可得。若有一法可得。即有一分识心结习未尽。识心结习若尽。则万法本具一心。一心本具万法。一心与万法不二。万法与本心本一。复何有所得哉。</p>
				<p>菩提萨。依般若波罗蜜多故。</p>
				<p>菩提萨。即菩萨之称。依。因也。谓依般若之本智本慧。自悟自修。以登彼岸之故。</p>
				<p>心无挂碍。无挂碍故。无有恐怖。</p>
				<p>挂者悬系。碍者窒塞。依一心之本智。不依缘起之业识。不住客尘之妄法。则心是空心。法皆空法。所谓不依一法而心常住。已得大自在矣。复何挂碍之有。</p>
				<p>恐怖。如地陷风飘。火烧水溺。及杀害恶死。种种之可惊恐怖畏者。既无挂碍。则不滞我相我见。四大皆空。五蕴非有。常清常静。不动不摇。一切如如。复何惊恐怖畏之有乎。</p>
				<p>远离颠倒梦想。究竟涅。</p>
				<p>颠倒。谓六尘惑乱。妄想成业。生灭旋复。虚妄轮转。种种颠倒也。梦想。谓惑于见思客尘。而迷梦妄想也。若既无恐怖。则障垢已尽。本性不复更迷。虚妄不复更生。复何三界感业而轮转颠倒。复何见思惑乱。而迷梦妄想乎。</p>
				<p>涅云不生。云不灭。谓本心本无挂碍。本无恐怖颠倒梦想。一切种种。皆缘起于无始无明虚妄业识耳。今既依当身一心本智。外息诸缘。内心无喘。心如墙壁。外不放入。内不放出。真常真乐。真我真净。究竟直到不生不灭涅之大道矣。</p>
				<p>三世诸佛。依般若波罗蜜多故。得阿耨多罗三藐三菩提。</p>
				<p>谓过去现在未来三世诸佛也。</p>
				<p>阿耨多罗。云无上也。三藐。云正等。三菩提。云正觉。绝诸对待。无有而上之者。故名阿耨多罗无上也。本心本智。各自具足。故名三藐正等也。本心般若智慧。本不为尘劳所蔽。故名三菩提正觉也。谓行深般若之本心本智。到波罗蜜之彼岸。而证菩提之果。不但观自在菩萨而然。即过去现在未来三世诸佛莫不依般若波罗蜜多故。得无上正等正觉之大道也。</p>
				<p>故知般若波罗蜜多。是大神咒。是大明咒。是无上咒。是无等等咒。能除一切苦。</p>
				<p>统妙万法。不可思议之谓神咒者。秘密心印也。</p>
				<p>普照一切。而无所不遍之谓明。</p>
				<p>最极一切。而无上可上之谓无上。</p>
				<p>一切平等。无等可等。谓无等等。</p>
				<p>谓度脱一切生死烦恼苦厄也。</p>
				<p>真实不虚。故说般若波罗蜜多咒。</p>
				<p>谓佛无妄语。如来所说是真语。实语。如语。不诳语。不异语也。</p>
				<p>以一心之本智本慧究竟。即尘劳是清净。即烦恼是菩提。一切苦厄莫不一齐解脱。真实不虚之故。乃说此本心本智。究竟自心秘密之总持也。</p>
				<p>即说咒曰。揭谛揭谛。波罗揭谛。波罗僧揭谛。菩提萨婆诃。</p>
				<p>大凡一切咒语。乃诸佛微妙秘密心印。惟当沉沉静静。无思无虑。一心持念。无记无数。即咒是心。即心是咒。不复以知见立知见。如此持念一心。至于知见无见。则群魔于此以降。真如于此以住。挂碍恐怖于此而解脱。颠倒梦想于此而远离。本智本慧于此而开发。无明烦恼于此而尽泯。即无明是大智。即烦恼是菩提。即菩提是五蕴。即五蕴是法身。即法身是诸法。即诸法是空相。即空相是当身一心。即当身一心是如来藏也。般若波罗蜜多心经心咒之功德。宁可思议哉。</p>
				<p>我佛释迦牟尼世尊。谓诸弟子曰。吾教中有观听圆明。智慧无碍。一心清净。如如不动之观自在菩萨。行深般若真空之智慧圆满。清净妙明之觉性。超越彼岸。具足慧定之时。</p>
				<p>以本心无碍智慧。照见色受想行识之五蕴皆空。度脱一切执著烦恼生死之苦厄。舍利子。汝在诸弟子之中。所称智慧第一者。</p>
				<p>而汝之智慧。何以不能究竟到照见五蕴皆空。度脱一切苦厄也。盖因滞于智慧而有我相我见。空相空见。不能如观自在菩萨。行深真空无相之般若。而一超直入菩提之觉岸也。夫五蕴之因有色。而后有受想行识。要知色乃梦幻泡影。不异夫空也。</p>
				<p>空乃一真显露不异夫色也。且色非离空而有色。性色真空。色不即是空乎。空非离色而断空。性空真色。空不即是色乎。</p>
				<p>色受想行识五者相因。究竟到色不异空。</p>
				<p>空不异色。色即是空。空即是色。而受想行识。亦复如是之究竟耳。舍利子。不但五蕴皆空。凡修一切菩提之善法。莫不皆空也。</p>
				<p>是诸法空相。始能见真空之实相。盖本妙明心本来真空无相。亦无法也。诸法不过为执迷滞塞者。降妄住真耳。若执著于法。则有法生。法生而心意意识之业。亦与之俱生。有生则有灭。有生灭。则有垢净增减矣。</p>
				<p>若一切法不生。则一切法空。如虚空界之空相。曾见有生灭垢净增减乎。是诸法空相。如是不生不灭。不垢不净。不增不减也。真空本无我相。何色之有。是故空中无色也。既无有色。自无我见。无受想行识也。我相既无。则六根自非我有。无眼耳鼻舌身意矣。六根既无。则六尘无安顿之所。无色声香味触法矣。夫眼为六根之先锋。意为六识之主帅。而六根六尘。无识不显。无眼界。</p>
				<p>
					乃至无意识界。则六根六尘六识之十八界限无也。五蕴十八界皆空。无见闻知觉。无心意意识。则三世十二因缘之无明妄识。一时皆尽无无明矣。既无无明。则无起无尽。亦无无明尽矣。因无明缘行。因行缘识。因识缘名色。因名色缘六入。因六入缘触。因触缘受。因受缘爱。因爱缘取。因取缘有。因有缘生。因生缘老死。十二因缘。起而复尽。尽而复起。循环三世。生灭无端。皆因无明之妄识流转。既无无明尽。如如不动之心。无生无灭。乃至十二因缘同归无无尽。亦无老死尽矣。究竟到无五蕴根尘处界。无十二因缘生死烦恼。种种之苦因集聚。则生灭既灭。寂灭现前。自得圆通三昧真常之道矣。其时已具明了无碍之智。已得圆觉菩提之果。似夫有智有得也。然般若无知。无所不知。无漏之智。如无智也。菩提之果。亦本心限量。即心即佛。不假外求。
				</p>
				<p>
					亦无所得也。以般若真空之本智本慧。不假外求。无所得故。是以菩萨依般若波罗蜜多真空之本智。不依缘起之妄尘。则心是空心。无挂碍矣。无挂碍故。则四大皆空。五蕴非有。常清常净。不动不摇。自无有恐怖。无恐怖。则障垢已尽。本性不复更迷。虚妄不复更生。远离轮转之颠倒。思惑之迷梦。客尘之妄想真常真乐。真我真净。究竟直入不生不灭涅之大道也。行深般若波罗蜜多。不但观自在菩萨为然。即过去现在未来三世诸佛。莫不依般若波罗蜜多之故。得无上正等正觉之阿耨多罗三藐三菩提。故知般若波罗蜜多。是不可思议秘密之心印。是普照一切秘密之心印。是至极无上可上秘密之心印。是一切平等不偏不倚秘密之心印。能除一切生死烦恼之苦厄。真实而非虚。我故说般若波罗蜜多咒。以度现在未来众生之苦厄。汝等不可以意识测度而思维。惟当无思无虑。一心持念。咒即是心。心即是咒。如此持念一心。至于知见无见。意识尽泯。群魔于此以降。智慧于此以开。挂碍恐怖于此解脱。颠倒梦想于此远离。即无明是大智。即烦恼是菩提。圆觉妙明之本心。普照无碍。其无量无边之功德。宁可思议哉。
				</p>
				<p>金刚经大旨。总是欲人守六根绝六尘。而六根六尘之不净者。以五蕴未空故也。</p>
				<p>观自在菩萨特为拈出。示后学以会归之路。文仅二百六十八字。而言简意赅。可包金刚经三十二分之全。读金刚经而不读心经。是涉博而不知反约也。乌乎可。</p>
			</div>
		</div>
	</div>
