到空相;也不要执着于空相，而看不到色相。")]),_c('p',[_vm._v("观自在菩萨所讲的是大乘佛法，圆通自在，空灵无碍，色空无二。")]),_c('p',[_vm._v("受、想、行、识，亦复如是：")]),_c('p',[_vm._v("受、想、行、识也和色一样，与空无分别。")]),_c('p',[_vm._v("诸法空相：")]),_c('p',[_vm._v("所有的法，都是空相。没有分别相。")]),_c('p',[_vm._v("不生不灭，不垢不净，不增不减：")]),_c('p',[_vm._v("既然是空相，那么也就没有生与死、垢与净、增与减的分别。")]),_c('p',[_vm._v("人的自性，本身就是空灵的，清净无碍的。众生迷惑，执着于一头，自然不知其理。")]),_c('p',[_vm._v("是故：")]),_c('p',[_vm._v("正是这个空的缘故。")]),_c('p',[_vm._v("观自在菩萨更进一步地向我们阐述一切皆空，这一玄奥的道理。")]),_c('p',[_vm._v("空中无色，无受、想、行、识：")]),_c('p',[_vm._v("色、受、想、行、识这五蕴也是空的。")]),_c('p',[_vm._v("无眼、耳、鼻、舌、身、意：")]),_c('p',[_vm._v("眼、耳、鼻、舌、身、意这六根也是空的。即六根清净。")]),_c('p',[_vm._v("无色、声、香、味、触、法：")]),_c('p',[_vm._v("色、声、香、味、触、法这六尘也是空的。")]),_c('p',[_vm._v("无眼界，乃至无意识界：")]),_c('p',[_vm._v("即十八界也是空的。")]),_c('p',[_vm._v("六根界：眼界、耳界、鼻界、舌界、身界、意界;")]),_c('p',[_vm._v("六尘界： 、声界、香界、味界、触界、法界;")]),_c('p',[_vm._v("六识界：眼识界、耳识界、鼻识界、舌识界、身识界、意识界。")]),_c('p',[_vm._v("无无明,亦无无明尽,乃至无老死,亦无老死尽：")]),_c('p',[_vm._v("即这十二因缘也都是空的。")]),_c('p',[_vm._v("十二因缘：")]),_c('p',[_vm._v("无明、行、识、名色、六入、触、受、爱、取、有、生、老死。")]),_c('p',[_vm._v("空中自然没有这十二因缘，也没有这十二因缘役使。")]),_c('p',[_vm._v("法句经中释迦说：")]),_c('p',[_vm._v("勤奋努力是不死的境界，懒惰懈怠是死境界。")]),_c('p',[_vm._v("勤奋努力虽死犹生，懒惰懈怠虽生犹死。")]),_c('p',[_vm._v("无苦、集、灭、道：")]),_c('p',[_vm._v("四谛也还是空的。")]),_c('p',[_vm._v("四谛：")]),_c('p',[_vm._v("苦：凡夫的现状，病状。")]),_c('p',[_vm._v("集：苦之原因，病因。")]),_c('p',[_vm._v("灭：觉悟状态，健康相。")]),_c('p',[_vm._v("道：实现理想的方法，治疗法。")]),_c('p',[_vm._v("无智也无得：")]),_c('p',[_vm._v("既然一切皆空，那么智和得也是空的了。")]),_c('p',[_vm._v("去法我执，即把人生的最高追求欲望，也要空掉，不要执著。")]),_c('p',[_vm._v("以无所得故：")]),_c('p',[_vm._v("正是因为没有什么可以得到的缘故。")]),_c('p',[_vm._v("只有明白了一切皆空的道理，本来就有的妙真如性，就会显现出来。妙真如性，是众生本身就具有，又何需得到?")]),_c('p',[_vm._v("金刚经说，无有少法可得。倘若有法可得，即著相。")]),_c('p',[_vm._v("凡所有相，皆是虚妄。就不是佛法。")]),_c('p',[_vm._v("只有破人我执，破法我执，把一切虚妄相破掉，心里头才湛然清净，无拘无执，成就佛道。")]),_c('p',[_vm._v("心无挂碍，无挂碍故，无有恐怖：")]),_c('p',[_vm._v("挂：牵挂。碍：阻碍。")]),_c('p',[_vm._v("因为依照般若波罗密多的方法修行，所以不为物所拘，无牵挂滞碍。")]),_c('p',[_vm._v("没有牵挂，也就没有恐怖心理。")]),_c('p',[_vm._v("人只要有滞碍、有牵挂，就永远无法摆脱苦恼的纠缠。")]),_c('p',[_vm._v("远离颠倒梦想:")]),_c('p',[_vm._v("正确地认识真理，远离一切错误想法和梦幻想法。")]),_c('p',[_vm._v("究竟涅盘：")]),_c('p',[_vm._v("究竟：毕竟，到底的意思。")]),_c('p',[_vm._v("涅盘：超越生死的境界。")]),_c('p',[_vm._v("开悟是人生的最终目标，因此尽形寿不懈怠，最终超越一切境相。")]),_c('p',[_vm._v("三世诸佛：")]),_c('p',[_vm._v("三世：过去世、现在世、未来世。")]),_c('p',[_vm._v("诸佛：一切佛。")]),_c('p',[_vm._v("阿耨多罗三藐三菩提：")]),_c('p',[_vm._v("阿耨多罗：无上的。")]),_c('p',[_vm._v("三藐三菩提：正等正觉，真正觉悟的人。")]),_c('p',[_vm._v("故知：")]),_c('p',[_vm._v("所以我们了解。")]),_c('p',[_vm._v("咒：即陀罗尼。真言，总持。总持：就是总纲。一法普现一切法，一咒普含一切咒。")]),_c('p',[_vm._v("大神咒：具有大神力的咒。")]),_c('p',[_vm._v("大明咒：具有大光明的咒。")]),_c('p',[_vm._v("无上咒：具有至高无上的咒。")]),_c('p',[_vm._v("无等等咒：具有无与伦比的咒。")]),_c('p',[_vm._v("能除一切苦：")]),_c('p',[_vm._v("它能除尽人心中的一切烦苦。")]),_c('p',[_vm._v("真实不虚：")]),_c('p',[_vm._v("是真实的，不是虚假的。")]),_c('p',[_vm._v("即说咒曰：")]),_c('p',[_vm._v("那么，就让我来读诵咒语吧。")]),_c('p',[_vm._v("揭谛揭谛,波罗揭谛,波罗僧揭谛,菩提萨婆诃!")]),_c('p',[_vm._v("此四句是密咒，要绝对明确地译出来是极为困难的，因此只需念诵，不需翻译。")]),_c('p',[_vm._v("如果强译，可理解为：")]),_c('p',[_vm._v("觉悟，觉悟，正在觉悟，已经觉悟，就这样成就了佛道。")]),_c('p',[_vm._v("波罗即到彼岸，波罗僧即已经到达彼岸者。")]),_vm._m(0),_c('p',[_vm._v("般若。智慧也。波罗蜜。到彼岸也。多。定也。经。径也。言此经乃定心之径路也。此经以单法为名。实相为体。观照为宗。度苦为用。大乘为教相。此五者。经中所说之旨也。单法者。即般若波罗蜜多也。实相者。即诸法空相也。观照者。即照见五蕴皆空妄也。度苦者。即度一切业报苦厄也。大乘者。即菩萨所行甚深般若也。")]),_c('p',[_vm._v("观自在菩萨。")]),_c('p',[_vm._v("观自在者。观听圆明。智慧无碍。观有不住有。观空不住空。心不能动。境不能随。动随不乱其真。得大自在圆通也。菩萨。即菩提萨之省文。菩提曰觉。萨曰有情。谓能觉一切有情。自觉以觉众生也。曰菩萨曰大士。皆尊称佛号之名也。观自在菩萨。即观世音大士也。")]),_c('p',[_vm._v("行深般若波罗蜜多时。")]),_c('p',[_vm._v("行深者。谓清净因地之法行已深也。般若。华言智慧。般若智慧。非识心之昭昭灵灵。乃本心真性之光明。照无量世界。通无量劫事。圆明普照之真智真慧也。波罗蜜。华言到彼岸。此岸。乃众生作孽受苦。堕落沉沦之地。彼岸。乃诸佛菩萨究竟到人欲尽净。光明正大之地。多者。定也。时者。时候也。谓菩萨妙圆明觉超越世出世间。具足三昧慧定之时也。")]),_c('p',[_vm._v("照见五蕴皆空。")]),_c('p',[_vm._v("照者。本心智慧妙觉明照也。见者。本心智慧明圆真见也。五蕴者。色受想行识之五阴也。空即本心性体实相之真空也。照见蕴空。则本心妙明而性体洞见也。")]),_c('p',[_vm._v("度一切苦厄。")]),_c('p',[_vm._v("谓解脱一切执著生死烦恼之苦厄。成就一切圆通也。")]),_c('p',[_vm._v("舍利子。")]),_c('p',[_vm._v("即舍利弗。母名舍利。因母立名。乃佛之弟子。舍利弗于佛弟子之中。智慧第一。而不能如观世音菩萨究竟得证圆通。而成正觉者。盖因滞于智慧也。滞于智慧。则有我相。我见。(空相。空见。)佛故举观自在菩萨之行深般若真空无相之法。以证菩提彼岸。得为圆明普照之标榜。呼舍利子而告之。欲其不滞于我相。我见。空相。空见。而究竟于人空法空。空空之境界。以证无上道也。")]),_c('p',[_vm._v("色不异空。空不异色。色即是空。空即是色。受想行识。亦复如是。")]),_c('p',[_vm._v("色乃梦幻泡影。故不异于空也。")]),_c('p',[_vm._v("空乃一真显露。故不异于色也。")]),_c('p',[_vm._v("色有形相。凡有所相。皆是虚妄。终有坏期。故色即是空也。")]),_c('p',[_vm._v("空性虚无。无形无声者也。道以虚无之体。视之不见。听之不闻。能生有色有声之天地万物。故空即是色也。")]),_c('p',[_vm._v("因色而有六根之领受。六根领受于心。而生思量拟度之想。悬想六尘。而生运用施为之行。行动迁流。而生分辨精粗美恶之识。色受想行识。五者相因也。色有坏时。而受想行识。终归虚妄。亦复如是。色即是空也。能了悟色即是空。逢色不受。则无受想行识诸杂念一心清净。空若太虚。以无相而生真空之实相。亦复如是空即是色也。")]),_c('p',[_vm._v("舍利子。是诸法空相。不生不灭。不垢不净。不增不减。")]),_c('p',[_vm._v("佛复呼舍利子之名。进一层以告之也。意谓不但五蕴皆空。一切修证菩提之法。莫不皆空。楞伽经云。一切法不生。如是一切法空。若执著于法。则有法生。法生则心意意识皆生。有生则有灭。有生灭。则有垢净增减矣。诸法若如虚空界之空相。岂有生灭垢净增减乎。")]),_c('p',[_vm._v("是故空中无色。无受想行识。无眼耳鼻舌身意。无色声香味触法。")]),_c('p',[_vm._v("是故承上起下之词。是故真空之中本无我相。何色之有。")]),_c('p',[_vm._v("既无我相。自无我见。一切声色何能入我真空妙明之心。自无受想行识矣。")]),_c('p',[_vm._v("既无我相。则六根亦非我有。无眼耳鼻舌身意矣。")]),_c('p',[_vm._v("六根既无。则六尘无安顿之所。自无色声香味触法矣。")]),_c('p',[_vm._v("无眼界。乃至无意识界。")]),_c('p',[_vm._v("眼为六根之先锋。意为六识之主帅。而六根六尘。无识不显。无眼界。乃至无意识界。则六根六尘六识之十八界皆无界限矣。")]),_c('p',[_vm._v("无无明。亦无无明尽。乃至无老死。亦无老死尽。")]),_c('p',[_vm._v(" 无明至老死。乃佛所说三世十二因缘也。一曰。无明。谓过去世。妄识迷性也。二曰行。谓过去世。一切结业。随无明妄识流转也。三曰识。谓现在世。托胎识灵种子也。四曰名色。谓现在世。识灵在胎。心但有名。而色身已有质也。五曰六入。谓现在世。识灵在胎。已成六根之体。已具六入之用也。六曰触。谓现在世。色身出胎之后。六根但有所触。尚未了知六尘之涉入也。七曰受。谓现在世。色身日渐长大。已能纳受六尘诸境。尚于贪淫等心未起也。八曰爱。谓现在世。色身日益长成。贪淫等心已开。而生种种爱欲。尚未实爱欲之境也。九曰取。谓现在世。色身强壮。爱欲日盛。而驰求恣取色声香味触等以实爱欲之境也。十曰有。谓现在世。色身因驰求恣取。六根积集欲界。 。无 。三界之因。成就欲有。色有。无色有。三有之业果也。十一曰生。谓因果不亡。识灵又随业流转。受生于未来世之四生六道也。十二曰老死。谓未来世。受生以后。色身衰老。又坏而死也。因无明缘行。因行缘识。因识缘名色。因名色缘六入。因六入缘触。因触缘受。因受缘爱。因爱缘取。因取缘有。因有缘生。因生缘老死。十二因缘。生灭无常。有起有尽。尽而复起。起而复尽。循环三世。生灭轮转。若观此十二因缘。皆为无明妄识流转。忽然觉悟五蕴十八界皆空。则本妙明心。如如不动。无有生灭之妄识。则无无明矣。既无无明。则无起无尽。亦无无明尽矣。乃至者。包无行亦无行尽。无识亦无识尽。无名色亦无名色尽。无六入亦无六入尽。无触亦无触尽。无受亦无受尽。无爱亦无爱尽。无取亦无取尽。无有亦无有尽。无生亦无生尽。无老死亦无老死尽。 ")]),_c('p',[_vm._v("无苦集灭道。无智亦无得。")]),_c('p',[_vm._v("苦。谓生死烦恼诸苦。集。谓积聚。人生在世。心为形役。形为事劳。积聚五蕴中之根尘处界。十二因缘生死烦恼。种种苦因。今既无五蕴根尘处界。十二因缘。则无苦因之积聚也。")]),_c('p',[_vm._v("灭。谓寂灭。道。谓真常不息之道。五蕴中一切苦因。一一皆生灭无常。今生灭既灭。寂灭现前。而当体妙明寂静之真性。洞彻圆通。以入真常不息之道矣。")]),_c('p',[_vm._v("谓既无苦集灭道。则已入佛慧。明了无碍之智。已得菩提之果。然虽入佛智。乃本心般若真空之本智。般若无知。无所不知。无漏之智。如无智也。虽得菩提之果。亦是本心现量。即心即佛。不假外求。亦无所得也。若知有智。乃曲心推测之知见。非真空无漏之本智也。若云有所得。即有所失。亦有漏也。本心具足本智。故云无智亦无得也。")]),_c('p',[_vm._v("以无所得故。")]),_c('p',[_vm._v("此承上起下之词。谓一心之本智本法。本自具足。无心外之法可得。若有一法可得。即有一分识心结习未尽。识心结习若尽。则万法本具一心。一心本具万法。一心与万法不二。万法与本心本一。复何有所得哉。")]),_c('p',[_vm._v("菩提萨。依般若波罗蜜多故。")]),_c('p',[_vm._v("菩提萨。即菩萨之称。依。因也。谓依般若之本智本慧。自悟自修。以登彼岸之故。")]),_c('p',[_vm._v("心无挂碍。无挂碍故。无有恐怖。")]),_c('p',[_vm._v("挂者悬系。碍者窒塞。依一心之本智。不依缘起之业识。不住客尘之妄法。则心是空心。法皆空法。所谓不依一法而心常住。已得大自在矣。复何挂碍之有。")]),_c('p',[_vm._v("恐怖。如地陷风飘。火烧水溺。及杀害恶死。种种之可惊恐怖畏者。既无挂碍。则不滞我相我见。四大皆空。五蕴非有。常清常静。不动不摇。一切如如。复何惊恐怖畏之有乎。")]),_c('p',[_vm._v("远离颠倒梦想。究竟涅。")]),_c('p',[_vm._v("颠倒。谓六尘惑乱。妄想成业。生灭旋复。虚妄轮转。种种颠倒也。梦想。谓惑于见思客尘。而迷梦妄想也。若既无恐怖。则障垢已尽。本性不复更迷。虚妄不复更生。复何三界感业而轮转颠倒。复何见思惑乱。而迷梦妄想乎。")]),_c('p',[_vm._v("涅云不生。云不灭。谓本心本无挂碍。本无恐怖颠倒梦想。一切种种。皆缘起于无始无明虚妄业识耳。今既依当身一心本智。外息诸缘。内心无喘。心如墙壁。外不放入。内不放出。真常真乐。真我真净。究竟直到不生不灭涅之大道矣。")]),_c('p',[_vm._v("三世诸佛。依般若波罗蜜多故。得阿耨多罗三藐三菩提。")]),_c('p',[_vm._v("谓过去现在未来三世诸佛也。")]),_c('p',[_vm._v("阿耨多罗。云无上也。三藐。云正等。三菩提。云正觉。绝诸对待。无有而上之者。故名阿耨多罗无上也。本心本智。各自具足。故名三藐正等也。本心般若智慧。本不为尘劳所蔽。故名三菩提正觉也。谓行深般若之本心本智。到波罗蜜之彼岸。而证菩提之果。不但观自在菩萨而然。即过去现在未来三世诸佛莫不依般若波罗蜜多故。得无上正等正觉之大道也。")]),_c('p',[_vm._v("故知般若波罗蜜多。是大神咒。是大明咒。是无上咒。是无等等咒。能除一切苦。")]),_c('p',[_vm._v("统妙万法。不可思议之谓神咒者。秘密心印也。")]),_c('p',[_vm._v("普照一切。而无所不遍之谓明。")]),_c('p',[_vm._v("最极一切。而无上可上之谓无上。")]),_c('p',[_vm._v("一切平等。无等可等。谓无等等。")]),_c('p',[_vm._v("谓度脱一切生死烦恼苦厄也。")]),_c('p',[_vm._v("真实不虚。故说般若波罗蜜多咒。")]),_c('p',[_vm._v("谓佛无妄语。如来所说是真语。实语。如语。不诳语。不异语也。")]),_c('p',[_vm._v("以一心之本智本慧究竟。即尘劳是清净。即烦恼是菩提。一切苦厄莫不一齐解脱。真实不虚之故。乃说此本心本智。究竟自心秘密之总持也。")]),_c('p',[_vm._v("即说咒曰。揭谛揭谛。波罗揭谛。波罗僧揭谛。菩提萨婆诃。")]),_c('p',[_vm._v("大凡一切咒语。乃诸佛微妙秘密心印。惟当沉沉静静。无思无虑。一心持念。无记无数。即咒是心。即心是咒。不复以知见立知见。如此持念一心。至于知见无见。则群魔于此以降。真如于此以住。挂碍恐怖于此而解脱。颠倒梦想于此而远离。本智本慧于此而开发。无明烦恼于此而尽泯。即无明是大智。即烦恼是菩提。即菩提是五蕴。即五蕴是法身。即法身是诸法。即诸法是空相。即空相是当身一心。即当身一心是如来藏也。般若波罗蜜多心经心咒之功德。宁可思议哉。")]),_c('p',[_vm._v("我佛释迦牟尼世尊。谓诸弟子曰。吾教中有观听圆明。智慧无碍。一心清净。如如不动之观自在菩萨。行深般若真空之智慧圆满。清净妙明之觉性。超越彼岸。具足慧定之时。")]),_c('p',[_vm._v("以本心无碍智慧。照见色受想行识之五蕴皆空。度脱一切执著烦恼生死之苦厄。舍利子。汝在诸弟子之中。所称智慧第一者。")]),_c('p',[_vm._v("而汝之智慧。何以不能究竟到照见五蕴皆空。度脱一切苦厄也。盖因滞于智慧而有我相我见。空相空见。不能如观自在菩萨。行深真空无相之般若。而一超直入菩提之觉岸也。夫五蕴之因有色。而后有受想行识